import {data as gData} from 'zhixin'
import {Indicator} from 'mint-ui'
import Http from 'Http'

function configUserData(data) {
    if (data) {
        gData.token = data.appToken//refer to wechat's openID
        const humans = data.humans || []
        const human = humans.length > 0 && humans[0] || data.human

        if (human) {
            gData.humanName = human.humanName
            gData.humanID = human.humanID
            gData.cardID = human.cardID
            gData.selfFlag = human.selfFlag
        }
    }
}

function verifyState({from, hideIndicator, inst}) {
    if (true) {
        return new Promise(fullfill=> {
            fullfill()
        })
    }
    return new Promise((fullfill, reject)=> {
        Http.ajax({
            methodName: '/api/v2/microvalid',
            params: {
                code: gData.wechatCode,
                appTypeID: 0//0：微信，1：qq
            },
            start: ()=> {
                if (!hideIndicator) {
                    Indicator.open()
                }
            },
            success: (data)=> {
                try {
                    if (data) {
                        if (!data.success) {
                            let openID = data.data && data.data.openID
                            if (openID && from && inst) {
                                gData.openID = openID
                                inst.$router.replace({
                                    name: 'register',
                                    params: {
                                        redirect: from
                                    }
                                })
                            } else if (!openID) {
                                alert("获取用户id失败，请重试")
                            }
                        } else {
                            configUserData(data.data)
                            fullfill()
                        }
                    }
                } catch (e) {
                    alert(JSON.stringify(e))
                }
            },
            final: ()=> {
                Indicator.close()
            }
        })
    })
}

export {
    verifyState, configUserData
}


