import zx from 'zhixin'
import Http from 'Http'
import Util from 'Util'
import Vue from 'vue'
import VueRouter from 'vue-router'
import LongClick from '../libs/directive/LongClick'

import {doConfig} from '../libs/wxUtil'

zx.setUp({
    showHeader: true,
    requestType: Http.TYPE.public_v14,
    debug: true
})

//使用import会被提升到前面，导致main.css起不到覆盖的作用
require('./main.css')

if (__DEBUG__) {
    // Http.mockServerUrl('http://192.168.32.218:8080/zxflat')
    // Http.mockServerUrl('http://192.168.32.47:8080/wsm')
    // Http.mockServerUrl('http://withparadox2server.tunnel.2bdata.com/zx')
    // Http.mockServerUrl('http://localhost:8089/zx')
    Http.mockServerUrl('http://192.168.32.167:8080/v14')//测试用市民通服务器
    // Http.mockServerUrl('http://zxgzh.egova.com.cn/zhixin')
}

Vue.use(LongClick)
Vue.prototype.isImage = function (mediaName) {
    return mediaName.endsWith(".jpg")
}

// doConfig()

// zx.data.shareMode = Util.getRequestParam('shareWechat') == '1'
// zx.data.fromWechat = Util.getRequestParam('fromWechat') == '1'
// zx.data.wechatCode = Util.getRequestParam('code')
// zx.data.shareUUID = Util.getRequestParam('uuID')
// zx.data.appID = Util.getRequestParam('appID')


// window.__WECHAT__ = zx.data.shareMode || zx.data.fromWechat


const Report = require('./view/report/Report.vue')
const SelectAddress = require('./view/SelectAddress.vue')
const Login = require('./view/login.vue')
const Register = require('./view/Register.vue')
const CaseList = require('./view/CaseList.vue')
const Plugins = require('./view/Plugins.vue')
const ReportList = require('./view/report/ReportList.vue')
const Poi = require('./view/poi/Poi.vue')
const PoiDetail = require('./view/poi/PoiDetail.vue')
const CaseDetail = require('./view/CaseDetail.vue')

const router = new VueRouter({
    scrollBehavior: () => ({x: 0, y: 0}),
    routes: [
        {path: '/', redirect: '/login'},
        {path: '/report/:reportType', name: 'report', component: Report},
        {path: '/selectAddress', name: 'selectAddress', component: SelectAddress},
        {path: '/login', name: 'login', component: Login},
        {path: '/register', name: 'register', component: Register},
        {path: '/caseList', name: 'caseList', component: CaseList},
        {path: '/plugins', name: 'plugins', component: Plugins},
        {path: '/reportList', name: 'reportList', component: ReportList},
        {path: '/poi/:poiName/:poiType', name: 'Poi', component: Poi},
        {path: '/poiDetail/:poiItem', name: 'PoiDetail', component: PoiDetail},
        {path: '/caseDetail', name: 'caseDetail', component: CaseDetail}
    ]
})

zx.setUpBackHandler(router, Util.getHash())

new Vue({
    router
}).$mount('#app')




