<template>
    <div class="mint-content">
        <div v-for="item in pluginList"
             class="plugin bdr-b"
             @click="goPlugin(item)">
            {{item.displayName}}
        </div>
    </div>
</template>
<style scoped>
    .plugin {
        padding: 20px;
        background-color: white;
    }
</style>
<script>
    import {PluginIdMap} from '../config'
    import Vue from 'vue'
    import Util from 'Util'
    export default{
        data(){
            return {
                pluginList: []
            }
        },
        methods: {
            goPlugin(plugin) {
                switch (plugin.PLUGINID) {
                    case PluginIdMap.report:
                        this.$router.push({
                            name: "reportList"
                        })
                        break
                    case PluginIdMap.dealHelper:
                        this.$router.push({
                            name: 'caseList'
                        })
                        break
                    case PluginIdMap.verifyCheck:
                        Vue.http.post("http://192.168.32.224:8080/zy/api/v2/rec/getTaskDetail?taskNum=201705090004&isJsonp=1")
                        break
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm=> {
                Util.setTitle('功能导航')
                if (vm.isNewPage(to)) {
                    let params = vm.$route.params
                    vm.pluginList = params && params.plugins || []
                }
            })
        }
    }
</script>



